/**
 * Reset CSS
 */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  background-color: transparent;
  color: inherit;
  text-decoration: none;
}

abbr {
  border-bottom: 0;
  cursor: help;
  font-variant: normal;
  text-decoration: none;
}

button {
  -webkit-appearance: button;
  border: 0;
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  overflow: visible;
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

img {
  vertical-align: middle;
}

main {
  display: block;
}

/**
 * Main CSS
 */
:root,
body {
  overflow-x: hidden;
}

:root {
  word-wrap: break-word;
  -moz-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  color: #83869a;
  font: 500 100% "Plus Jakarta Sans", fbPC, fbAndroid, fbiOS, sans-serif;
  min-width: 10.625rem;
  overflow-wrap: anywhere;
  text-rendering: optimizeSpeed;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
          text-size-adjust: 100%;
}

body {
  font-size: 112.5%;
  height: 100vh;
  line-height: 1.5556;
}

.scr-hide {
  left: -100vw;
  max-width: 20px;
  max-height: 20px;
  overflow: hidden;
  position: absolute;
}

.col {
  display: flex;
  flex-direction: column;
}

.row-stretch {
  flex: 1;
}

.wrapper {
  margin: 0 auto;
  max-width: 78.75rem;
}

.content {
  padding: 0 5.9524%;
}

.logo {
  font-size: 2.25rem;
  font-weight: 800;
  line-height: 1;
}

.btn {
  color: #fff;
  font-weight: 700;
  text-align: center;
  text-transform: capitalize;
  width: 10.4375rem;
}

.btn:hover {
  transition: background 0.3s ease;
}

.bar {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.bar .btn {
  border-radius: 1.75rem;
  line-height: 1.75rem;
  padding: 0.875rem 0;
}

.header {
  margin-bottom: 3.9375rem;
  padding: 1.5rem 0;
}

.header .logo {
  color: #13183f;
}

.header .btn {
  background: #13183f;
}

.header .btn:hover {
  background: #666ca3;
}

.intro {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 8.375rem;
}

.intro-verbal {
  flex: 0 28.5625rem;
  margin-bottom: 1.9375rem;
}

.intro-title {
  color: #13183f;
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 4.4375rem;
  margin-bottom: 1.8125rem;
}

.intro-txt {
  margin-bottom: 2.5rem;
  max-width: 97.3742%;
}

.intro .btn {
  background: linear-gradient(180deg, #ff6f48 0%, #f02aa6 100%);
  border-radius: 1.9688rem;
  padding: 1.1875rem 0 1rem;
}

.intro .btn:hover {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(180deg, #ff6f48 0%, #f02aa6 100%);
}

.intro-visual {
  flex: 0;
  margin: 0 4.8649%;
  position: relative;
}

.intro-img-cover {
  background: linear-gradient(180deg, #ff6f48 0%, #f02aa6 100%);
  border-radius: 9.9063rem;
  height: 35rem;
  overflow: hidden;
  position: relative;
  width: 19.8125rem;
}

.intro-img {
  bottom: 0;
  height: auto;
  position: absolute;
  right: 0;
  width: 110.4101%;
}

.intro-img-attach {
  background: linear-gradient(180deg, #4851ff 0%, #f02aa6 100%);
  border-radius: 9.9063rem;
  bottom: 0;
  height: 63rem;
  left: 0;
  position: absolute;
  transform: rotate(45deg) translate(-9.9063rem, -4.2146rem);
  transform-origin: bottom left;
  width: 19.8125rem;
}

.intro-stats {
  background: #fff;
  border-radius: 0.9375rem;
  box-shadow: 0 2.125rem 3.375rem rgba(6, 22, 141, 0.158854);
  padding: 1.25rem 1.5rem;
  position: absolute;
  white-space: nowrap;
}

.intro-stats > dt {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.75rem;
  margin-bottom: 0.125rem;
}

.intro-stats > dd {
  color: #13183f;
  font-size: 2.5rem;
  font-weight: 800;
  line-height: 3.125rem;
  overflow: hidden;
  text-overflow: ellipsis;
}

.stats-hours {
  bottom: 6.125rem;
  left: -7.5rem;
  width: 12.4375rem;
}

.stats-members {
  right: -7.375rem;
  top: 6.4375rem;
  width: 9.6875rem;
}

.row-course {
  background: linear-gradient(180deg, #fff 0%, #f0f1ff 100%);
}

.course-list {
  grid-gap: 5rem 1.875rem;
  display: grid;
  gap: 5rem 1.875rem;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 8.75rem;
}

.course {
  background: #fff;
  border-radius: 0.9375rem;
  box-shadow: 0 1.5625rem 3.125rem rgba(6, 22, 141, 0.0442381);
  display: flex;
  flex-direction: column;
  padding: 4rem 2rem 2.5rem;
  position: relative;
}

.course-list-title {
  background: linear-gradient(180deg, #ff6f48 0%, #f02aa6 100%);
  box-shadow: none;
  color: #fff;
  font-size: 2rem;
  font-weight: 800;
  line-height: 2.5rem;
}

.course-title,
.course-txt {
  margin-bottom: 1.5rem;
}

.course-title {
  color: #13183f;
  font-size: 1.5rem;
  font-weight: 800;
  line-height: 1.875rem;
  white-space: nowrap;
}

.course-txt {
  flex: 1;
}

.course-link {
  color: #f74780;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.75rem;
  text-transform: capitalize;
}

.course-link:hover {
  color: #ffa7c3;
  transition: color 0.3s ease;
}

.course-icon {
  align-items: center;
  background: linear-gradient(180deg, #ff6f48 0%, #f02aa6 100%);
  border-radius: 50%;
  display: flex;
  height: 3.5rem;
  justify-content: center;
  position: absolute;
  top: -1.5rem;
  width: 3.5rem;
}

.row-footer {
  background: #13183f;
}

.footer {
  color: #fff;
  padding: 2rem 0;
  text-align: center;
}

.footer .btn {
  background: linear-gradient(180deg, #4851ff -54.32%, #f02aa6 100%);
  padding: 0.875rem 0;
}

.footer .btn:hover {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(180deg, #4851ff -54.32%, #f02aa6 100%);
}

.attribution-wrap {
  flex: 100%;
  padding-top: 2rem;
}

.attribution {
  border-top: 0.125rem solid #fff;
  display: inline-block;
  font-size: 74%;
  padding: 1rem 2rem 0;
}

.attribution a {
  color: #f74780;
  white-space: nowrap;
}

.attribution abbr {
  cursor: inherit;
}

@media (min-width: 120.625rem) {
  .intro-img-attach {
    height: 80rem;
  }
}
@media (max-width: 61.25rem) {
  .stats-hours {
    bottom: 2rem;
    left: -6.25rem;
  }
}
@media (max-width: 53.75rem) {
  body {
    font-size: 1rem;
    line-height: 1.625;
  }
  .content {
    padding: 0 5.1433%;
  }
  .logo {
    font-size: 1.75rem;
  }
  .bar .btn {
    padding: 0.5625rem 0 0.6875rem;
    width: 8.75rem;
  }
  .header {
    margin-bottom: 1.6106rem;
  }
  .intro {
    margin-bottom: 10.0438rem;
  }
  .intro-verbal {
    flex-basis: 24.875rem;
    margin-bottom: 0;
  }
  .intro-title {
    font-size: 2.5rem;
    line-height: 3.125rem;
    margin-bottom: 1.5625rem;
  }
  .intro-txt {
    margin-bottom: 1.5rem;
    max-width: 100%;
  }
  .intro .btn {
    padding: 0.9375rem 0;
  }
  .intro-visual {
    margin: 0 5.9724% 0 2.9028%;
  }
  .intro-img-cover {
    height: 23.9082rem;
    width: 13.5338rem;
  }
  .intro-img-attach {
    height: 43.0348rem;
    transform: rotate(45deg) translate(-6.7669rem, -2.879rem);
    width: 13.5338rem;
  }
  .intro-stats {
    border-radius: 0.625rem;
    padding: 1rem 1.25rem;
  }
  .intro-stats > dt {
    font-size: 0.875rem;
    margin-bottom: 0;
  }
  .intro-stats > dd {
    font-size: 2rem;
    line-height: 2.5rem;
  }
  .stats-hours {
    bottom: -2.375rem;
    left: -2.8313rem;
    width: 9.6875rem;
  }
  .stats-members {
    right: -3.1969rem;
    top: -0.8625rem;
    width: 6.5625rem;
  }
  .course-list {
    grid-gap: 3.5rem 0.625rem;
    gap: 3.5rem 0.625rem;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 5.375rem;
  }
  .course {
    border-radius: 0.625rem;
    padding: 3.5rem 1.3125rem 2rem 2rem;
  }
  .course-list-title {
    font-size: 1.5rem;
    line-height: 2rem;
  }
  .course-title {
    font-size: 1.25rem;
    line-height: 1.5625rem;
    margin-bottom: 1rem;
  }
  .footer {
    padding: 2.25rem 0;
  }
}
@media (max-width: 33.75rem) {
  .content {
    padding: 0 4.2667%;
  }
  .header {
    margin-bottom: 1.375rem;
    padding: 1rem 0;
  }
  .intro {
    display: block;
    margin-bottom: 5.625rem;
  }
  .intro-verbal {
    margin-bottom: 4.375rem;
  }
  .intro-title {
    margin-bottom: 1.625rem;
  }
  .intro-visual,
  .intro-img-cover {
    height: 15.8125rem;
    width: 15.8125rem;
  }
  .intro-visual {
    margin: 0 auto;
  }
  .intro-img-cover {
    text-align: center;
  }
  .intro-img-attach {
    display: none;
  }
  .intro-img {
    position: relative;
    top: 1rem;
    width: 94.3557%;
  }
  .stats-hours {
    bottom: -1.5rem;
    left: -2.3125rem;
  }
  .stats-members {
    right: -2.3125rem;
    top: -1.5rem;
  }
  .course-list {
    display: block;
    margin-bottom: 5rem;
  }
  .course {
    padding: 3.5rem 1.8125rem 2rem 1.75rem;
    margin-bottom: 2.5rem;
  }
  .course-list-title {
    padding: 1.5rem 1.8125rem 2rem 1.75rem;
  }
}
@media (max-width: 18.75rem) {
  .stats-members {
    right: -1.25rem;
    top: -3.25rem;
  }
  .stats-hours {
    bottom: -2.25rem;
    left: -1.25rem;
  }
  .course {
    padding-left: 9.3333%;
    padding-right: 9.3333%;
  }
}
@media (max-width: 17.5rem) {
  .content {
    padding-left: 0 6%;
  }
  .intro-visual,
  .intro-img-cover {
    width: 100%;
  }
  .bar {
    flex-direction: column;
  }
  .bar .btn {
    margin-top: 1rem;
  }
}
@media (max-width: 13.75rem) {
  .intro .btn {
    width: 100%;
  }
  .stats-members {
    top: -4rem;
  }
  .intro-img {
    position: absolute;
    width: 100%;
    bottom: 0;
  }
}